                <div class="card-actions">
                  <el-button type="primary" text bg :icon="Download" @click.stop="downloadDocument(doc)"></el-button>
                   <!-- <el-dropdown trigger="click" @click.stop>
                    <el-button text bg class="more-button">
                      <el-icon><MoreFilled /></el-icon>
                  </el-button>
                  <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item @click="editDocument(doc)" :icon="Edit"></el-dropdown-item>
                        <el-dropdown-item @click="deleteDocument(doc)" :icon="Delete" class="delete-item"></el-dropdown-item>
