      :before-close="handleCloseUploadDialog"
    >
      <el-form ref="uploadFormRef" :model="uploadForm" :rules="uploadRules">
        <el-form-item label="" prop="title">
          <el-input v-model="uploadForm.title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="uploadForm.description" type="textarea" :rows="3" placeholder=""></el-input>
        </el-form-item>
