            drag
            action="javascript:void(0);"
            :auto-upload="false"
            :on-change="handleFileChange"
            :on-remove="handleRemove"
            :limit="1"
            :before-upload="beforeUpload"
            :on-error="handleUploadError"
            ref="uploadRef"
          >
            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
            <div class="el-upload__text">
              <em></em>
            </div>
            <template #tip>
              <div class="el-upload__tip">
                <strong></strong> txt, pdf, doc, docx, md, ppt, pptx, xls, xlsx 10MB
              </div>
            </template>
          </el-upload>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="handleCancelUpload"></el-button>
          <el-button type="primary" @click="submitUpload" :loading="uploading"></el-button>
