            </el-button> -->
          </div>
          
          <div v-if="categoryLoading" class="text-center p-4">
            <el-icon class="is-loading"><Loading /></el-icon> ...
          </div>
          
          <div v-else-if="categories.length <= 1" class="empty-categories">
            <el-empty description="" />
