            <el-button size="small" @click="cancelCategoryForm">
              
            </el-button>
          </div>
          
          <el-form :model="categoryForm" label-width="80px">
            <el-form-item label="" required>
              <el-input v-model="categoryForm.name" placeholder=""></el-input>
            </el-form-item>
            
            <el-form-item label="">
              <el-input type="textarea" v-model="categoryForm.description" :rows="2" placeholder=""></el-input>
            </el-form-item>
            
            <el-form-item label="">
              <el-select v-model="categoryForm.icon" placeholder="" style="width: 100%">
                <el-option value="Document" label="">
                  <div class="d-flex align-items-center">
                    <el-icon><Document /></el-icon> <span class="ml-2"></span>
                  </div>
                </el-option>
                <el-option value="Files" label="">
                  <div class="d-flex align-items-center">
                    <el-icon><Files /></el-icon> <span class="ml-2"></span>
                  </div>
                </el-option>
                <el-option value="Tickets" label="">
                  <div class="d-flex align-items-center">
                    <el-icon><Tickets /></el-icon> <span class="ml-2"></span>
                  </div>
                </el-option>
                <el-option value="DataAnalysis" label="">
                  <div class="d-flex align-items-center">
                    <el-icon><DataAnalysis /></el-icon> <span class="ml-2"></span>
                  </div>
                </el-option>
                <el-option value="PictureFilled" label="">
                  <div class="d-flex align-items-center">
                    <el-icon><PictureFilled /></el-icon> <span class="ml-2"></span>
                  </div>
                </el-option>
              </el-select>
            </el-form-item>
            
            <el-form-item label="">
              <el-color-picker v-model="categoryForm.color" show-alpha @change="formatColorValue"></el-color-picker>
              <span class="ml-2">{{ categoryForm.color }}</span>
