              <el-button @click="cancelCategoryForm"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </el-dialog>

    <!--  -->
    <KnowledgeBuilder 
      v-model="buildingVisible"
      @build-completed="handleBuildCompleted"
      @build-failed="handleBuildFailed"
    />

    <!--  -->
    <el-dialog
      v-model="editDialogVisible"
      title=""
      width="500px"
      :before-close="handleCloseEditDialog"
    >
      <el-form :model="editForm" label-width="80px">
        <el-form-item label="">
          <el-input v-model="editForm.title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.description" type="textarea" :rows="3" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="editForm.category" placeholder="" style="width: 100%">
            <el-option
              v-for="(cat, idx) in categories.filter(c => c.id !== 'all')"
              :key="idx"
              :label="cat.name"
              :value="cat.id"
            ></el-option>
          </el-select>
