        // (rgba)hex
        if (color.startsWith('rgba')) {
          const hexColor = color.replace(/rgba\((\d+),\s*(\d+),\s*(\d+),[^)]+\)/, (match, r, g, b) => {
            const toHex = (c) => {
              const hex = parseInt(c).toString(16);
              return hex.length === 1 ? '0' + hex : hex;
            };
            return `#${toHex(r)}${toHex(g)}${toHex(b)}`;
          });
          categoryForm.color = hexColor.substring(0, 20);
        } else {
          // 
          categoryForm.color = color.substring(0, 20);
        }
        console.log(`: ${color} -> ${categoryForm.color}`);
      }
    };
    
    // 
    const forceRefresh = async () => {
      console.log('');
      
      // 
      internalRefreshKey.value = Date.now();
      
      // 
      currentPage.value = 1;
      
      // DOM
      try {
        // 
        const overlays = document.querySelectorAll('.el-overlay, .el-overlay-dialog, .el-overlay-message-box, .el-popup-parent--hidden');
        overlays.forEach(el => {
          if (el && el.parentNode) {
            try {
              el.parentNode.removeChild(el);
            } catch (e) {
              console.error(':', e);
            }
          }
        });
        
        // body
        if (document && document.body) {
          document.body.style.overflow = '';
          document.body.classList.remove('el-popup-parent--hidden');
          document.body.style.pointerEvents = 'auto';
        }
        
        // 
        const dropdowns = document.querySelectorAll('.el-dropdown-menu, .el-select-dropdown');
        dropdowns.forEach(el => {
          if (el && el.parentNode) {
            try {
              el.parentNode.removeChild(el);
            } catch (e) {
              console.error(':', e);
            }
          }
        });
      } catch (error) {
        console.error('DOM:', error);
      }
      
      // 
      await nextTick();
      try {
        // 
        const interactiveElements = document.querySelectorAll(
          '.knowledge-container button, ' +
          '.el-dropdown-menu__item, ' +
          '.document-item-card, ' +
          '.card-actions button, ' +
          '.card-footer button, ' +
          '.el-dropdown, ' +
          '.category-item'
        );
        
        interactiveElements.forEach(el => {
          if (el) {
            // 
            el.style.pointerEvents = 'auto';
            if (el.hasAttribute('disabled') && !el.classList.contains('is-disabled')) {
              el.removeAttribute('disabled');
            }
            
            // 
            // Vue
            try {
              const parent = el.parentNode;
              if (parent) {
                // Vue
                el.style.opacity = '1';
                el.style.visibility = 'visible';
                el.style.display = '';
              }
            } catch (e) {
              console.error(':', e);
            }
          }
        });
        
        // 
        if (uploadRef.value) {
          uploadRef.value.clearFiles();
          uploadForm.file = null;
          uploadForm.title = '';
          uploadForm.description = '';
        }
        
        // 
        document.querySelectorAll('.document-item-card').forEach(card => {
          if (card) {
            card.style.pointerEvents = 'auto';
            card.style.cursor = 'pointer';
            // z-index
            card.style.zIndex = '1';
            card.style.position = 'relative';
          }
        });
        
        // 
        document.querySelectorAll('.el-dropdown').forEach(dropdown => {
          if (dropdown) {
            dropdown.style.pointerEvents = 'auto';
          }
        });
      } catch (error) {
        console.error(':', error);
      }
      
      // 
      try {
        console.log('...');
        loading.value = true;
        
        // 
        await fetchCategories().catch(err => {
          console.error(':', err);
          return [];
        });
        
        // 
        await fetchDocuments().catch(err => {
          console.error(':', err);
          return [];
        });
        
        console.log('');
      } catch (error) {
        console.error(':', error);
      } finally {
        loading.value = false;
      }
      
      // DOM
      await nextTick();
      setTimeout(() => {
        try {
          // 
          document.querySelectorAll('.knowledge-container button, .el-dropdown, .document-item-card').forEach(el => {
            if (el) {
              el.style.pointerEvents = 'auto';
            }
          });
        } catch (error) {
          console.error('DOM:', error);
        }
      }, 300);
    };
    
    // 
    watch(() => route.fullPath, (newPath) => {
      if (newPath === '/knowledge') {
        console.log('');
        // DOM
        setTimeout(() => {
          forceRefresh();
        }, 100);
      }
    });
    
    // 
    onMounted(() => {
      console.log('Knowledge');
      // 
      forceRefresh();
      });
    
    // keep-alive
    onActivated(() => {
      console.log('Knowledge');
      // 
      forceRefresh();
    });
    
    onBeforeUnmount(() => {
      // 
      // if (pollingInterval) {
      //   clearInterval(pollingInterval)
      // }
    })
    
    return {
      searchQuery,
      selectedCategory,
      categories,
      documents,
      filters,
      currentPage,
      pageSize,
      totalDocs,
      uploadVisible,
      uploadForm,
      uploadFormRef,
      uploadRef,
      uploadRules,
      uploading,
      loading,
      viewMode,
      sortOrder,
      showCategoryDialog,
      showAddCategoryForm,
      editingCategory,
      categoryForm,
      categoryLoading,
      showChunksDialog,
      selectedDocument,
      documentChunks,
      chunksLoading,
      isAdmin,
      currentCategoryName,
      processingCount,
      forceRefresh,
      internalRefreshKey,
      
      // 
      Search,
      Upload,
      FolderAdd,
      View,
      Download,
      More,
      Share,
      Edit,
      EditPen,
      FolderOpened,
      Star,
      Delete,
      Document,
      Files,
      Tickets,
      DataAnalysis,
      PictureFilled,
      UploadFilled,
      Loading,
      List,
      Grid,
      Crop,
      Plus,
      InfoFilled,
      Clock,
      User,
      Refresh,
      
      // 
      selectCategory,
      handleSearch,
      applyFilters,
      getFileIcon,
      formatDate,
      formatFileSize,
      getTagsList,
      previewDocument,
      downloadDocument,
      shareDocument,
      editDocument,
      handleUpdateDocument,
      handleCloseEditDialog,
      handleCancelEditDialog,
      deleteDocument,
      handleFileChange,
      handleRemove,
      beforeUpload,
      handleUploadError,
      submitUpload,
      handlePageChange,
      handleSizeChange,
      showDocumentChunks,
      editCategory,
      handleDeleteCategory,
      saveCategoryForm,
      cancelCategoryForm,
      resetCategoryForm,
      buildingVisible,
      handleBuildCompleted,
      handleBuildFailed,
      resetUploadForm,
      handleCloseUploadDialog,
      openUploadDialog,
      handleCancelUpload,
      formatColorValue,
      editDialogVisible,
      editForm
    }
  }
}
</script>

